import * as Yup from 'yup';

export const schema = Yup.object().shape({
    resourceUrl: Yup.string().required('O campo comprovante  obrigatorio'),
    expenseTypeCode: Yup.object().shape({
        label: Yup.string().required(),
        value: Yup.string().required('O campo tipo  obrigatorio'),
    }),
    currencyCode: Yup.object().shape({
        label: Yup.string().required(),
        value: Yup.string().required('O campo moeda  obrigatorio'),
    }),
    notes: Yup.string().required('O campo titulo da despesa  obrigatorio'),
    cardDate: Yup.string().required(
        'O campo data do comprovante  obrigatorio',
    ),
    amountTotal: Yup.number().required('O campo valor da nota  obrigatorio'),
    amountSpent: Yup.number().required(
        'O campo valor a ser considerado  obrigatorio',
    ),
});

export const optionsExpenseTypes = [
    { label: 'Selecione', value: '' },
    { label: 'Taxa de hotel', value: 'hotel-fee' },
    { label: 'Comida', value: 'food' },
    {
        label: 'Transporte',
        value: 'transport',
    },
];

export const optionsCurrencyCode = [
    { label: 'Selecione', value: '' },
    { label: 'BRL', value: 'BRL' },
    { label: 'USD', value: 'USD' },
    {
        label: 'MXN',
        value: 'MXN',
    },
];
